*{
	margin: 0;
	padding: 0;
}

html, body{
	width: 100%;
	height: 100%;
	background-color: beige;
}

header{
	text-align: center;
	height: 10vh;
}

.btnList{
  width: 20vh;
  height: 80vh;
  display: flex;
  flex-wrap: wrap;
  float: left;
}

.btn{
  width: 12vh;
  height: 5.5vh;
	background: linear-gradient(white, black);
	color: navy;
	font-weight: bold;
	text-shadow: 1px 1px white;
	font-size: 1.02em;
}

.main{
	height: 80vh;
	padding-left: 50px;
}

footer{
	text-align: center;
	height: 7vh;
}


#pixelsToPercents, #widthValue, #pixelsToPercentsHeight, #heightValue{
	text-align: center;
	font-weight: bold;
}

#widthValue, #heightValue{
	font-size: 1.5em;
	text-shadow: 2px 1px white;
}

#scoreWidth{
	height: 6%;
}

#pixelsToPercentsHeight{
	margin-top: 5%;
}

.effect3dHeader{
	color: #fff;
		text-shadow: 0px 1px 0px #999, 0px 2px 0px #888, 0px 3px 0px #777, 0px 4px 0px #666, 0px 5px 0px #555, 0px 6px 0px #444, 0px 7px 0px #333, 0px 8px 7px #001135;
	}

.subTitle{
		font-weight: bold;
		padding-top: 0.5%;
		text-shadow: 2px 1px white;
		font-size: 1.1em;
	}

	.effect3dFooter{
		font-size: 2.75em;
		margin-top: -2%;
		text-align: center;
		color: #0000bf;
			text-shadow: 0px 1px 0px #aaaae9, 0px 2px 0px #8d8de2, 0px 3px 0px #7171db, 0px 4px 0px #5555d4, 0px 5px 0px #3838cd, 0px 6px 0px #1c1cc6, 0px 7px 0px #0000bf;
		}
